body {
  --main-color: #00acdc;
  margin: 0;
  font-family: Roboto, Arial, Helvetica, sans-serif;
}

.header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header-logo {
  max-width: 40px;
  max-height: 40px;
}

.header-img {
  width: 100%;
}

.menu-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.menu-item:not(:last-child) {
  margin-right: 20px;
}

.menu-link {
  white-space: nowrap;
  color: #000;
  text-transform: uppercase;
  text-align: center;
  align-items: center;
  padding: 23px 0;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--main-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.menu-link:hover, .is-active {
  color: var(--main-color);
}

